import React from 'react';
import './OurValuesHome.scss';
import data from '../../Data/data.json';
import ourValuesHome from '../../Img/OurValuesHome/our-values.jpg';
import ourValuesHome1 from '../../Img/OurValuesHome/our-values-1.jpg';





const OurValuesHome = () => {
   

    return (
        <div className="container-fluid">
            <div className="row">           
                <div className="order-md-1 order-2 col-md-6 p-0 d-flex align-items-center">
                    <div className="ourValuesHome__left">
                        <h3 className="h3">{data.ourValuesHome.title}</h3>
                        <hr />
                        <div className="row">
                            <div className="col-md-4">
                                <p className="mt-5 ml-5 ourValuesHome__icon">{data.ourValuesHome.value1}</p>
                            </div>
                            <div className="col-md-4">
                                <p className="mt-5 ml-5 ourValuesHome__icon">{data.ourValuesHome.value2}</p>
                            </div>
                            <div className="col-md-4">
                                <p className="mt-5 ml-5 ourValuesHome__icon">{data.ourValuesHome.value3}</p>
                            </div>
                        </div>
                    </div>
                </div>
                <div className="order-md-2 order-1 col-md-6 p-0">
                    <img className="w-100" src={ourValuesHome} alt={data.ourValuesHome.title} />
                </div>
            </div>
            <div className="row">
                <div className="col-md-6 p-0">
                    <img className="w-100" src={ourValuesHome1} alt={data.ourValuesHome.title} />
                </div>         
                <div className="col-md-6 p-0 d-flex align-items-center">
                    <div className="ourValuesHome__left">
                        <h3 className="h3">{data.ourValuesHome.title2}</h3>
                        <hr />
                        <div className="row">
                            <div className="col-md-4">
                                <p className="mt-5 ml-5 ourValuesHome__icon">{data.ourValuesHome.value4}</p>
                            </div>
                            <div className="col-md-4">
                                <p className="mt-5 ml-5 ourValuesHome__icon">{data.ourValuesHome.value5}</p>
                            </div>
                            <div className="col-md-4">
                                <p className="mt-5 ml-5 ourValuesHome__icon">{data.ourValuesHome.value6}</p>
                            </div>
                        </div>
                    </div>
                </div>
                
            </div>
        </div>
    )
}

export default OurValuesHome;