import React from 'react';
import './AboutUs.scss';
import aboutUs from '../../Img/AboutUs/aboutUs.jpg';
import data from '../../Data/data.json';



const AboutUs = () => {

    return (
        <div className="container">
            <div className="row aboutUs">
                <div className="col-md-6 aboutUs__img">
                    <img className="w-100" src={aboutUs} alt="about us" />
                </div>
                <div className="col-md-6 aboutUs__content">
                    <h2 className="h2">{data.aboutUs.title}</h2>
                    <hr />
                    <h3>{data.aboutUs.subtitle}</h3>
                    <p className="mt-5">{data.aboutUs.desc}</p>
                </div>
            </div>
        </div>
    )
}

export default AboutUs;