import React from 'react';
import './Hero.scss';
import Bounce from 'react-reveal/Bounce';

import hero from '../../Img/Hero/hero.jpg';
import data from '../../Data/data.json';



const Hero = () => {
   

    return (
        <div className="hero d-flex align-items-center" style={{backgroundImage: `url(${hero})`}}>
            <div className="hero__content">
                <Bounce left>
                    <h1>{data.hero.title}</h1>
                </Bounce>
                <Bounce left delay="500">
                    <h3 className="h3">{data.hero.subtitle}</h3>
                </Bounce>
                
            </div>
        </div>
    )
}

export default Hero;