import React from 'react';
import './Contact.scss';
import data from '../../Data/data.json';



const Contact = () => {

    return (
        <div className="container contact">
            <div className="contact__title px-1">
                <h2 className="h2">Potrebne su Vam dodatne informacije?</h2>
                <h3 className="h3">Kontaktirajte nas!</h3>
            </div>
            <div className="row contact__content">
                
                
                <div className="col-md-4">
                    <div className="contact__card mb-5 d-flex align-items-end bg-light-gray">
                        <div className="contact__info">
                            <h5>{data.aboutUsTeam.person1.subtitle}</h5>
                            <h3 className="h3">{data.aboutUsTeam.person1.name}, {data.aboutUsTeam.person1.title}</h3>
                            <p><a className="aboutUsTeam__link" href = {"tel:" + data.aboutUsTeam.person1.phone}>{data.aboutUsTeam.person1.phone}</a></p>
                            <p><a className="aboutUsTeam__link" href = {"mailto:" + data.aboutUsTeam.person1.email}>{data.aboutUsTeam.person1.email}</a></p>
                        </div>
                    </div>
                </div>
                <div className="col-md-8">
                    <a className="contact__map-link" href={data.url} target="_blank" rel="noreferrer">
                        <div className="contact__card mb-5 d-flex align-items-end bg-blue contact__pin">
                            <div className="contact__info text-white">
                                <h5>Naša adresa</h5>
                                <h3 className="h3 text-white">{data.address}</h3>
                                <p><a className="aboutUsTeam__link link-white" href = {"tel:" + data.phone}>{data.phone}</a></p>
                                <p><a className="aboutUsTeam__link link-white" href = {"mailto:" + data.mail}>{data.mail}</a></p>
                            </div>
                        </div>
                    </a>
                </div>
                <div className="col-md-6">
                    <div className="contact__card mb-5 d-flex align-items-end bg-light-gray">
                        <div className="contact__info">
                            <h5>{data.aboutUsTeam.person3.subtitle}</h5>
                            <h3 className="h3">{data.aboutUsTeam.person3.name}, {data.aboutUsTeam.person3.title}</h3>
                            <p><a className="aboutUsTeam__link" href = {"tel:" + data.aboutUsTeam.person3.phone}>{data.aboutUsTeam.person3.phone}</a></p>
                            <p><a className="aboutUsTeam__link" href = {"mailto:" + data.aboutUsTeam.person3.email}>{data.aboutUsTeam.person3.email}</a></p>
                        </div>
                    </div>
                </div>
                <div className="col-md-6">
                    <div className="contact__card mb-5 d-flex align-items-end bg-light-gray">
                        <div className="contact__info">
                            <h5>{data.aboutUsTeam.person2.subtitle}</h5>
                            <h3 className="h3">{data.aboutUsTeam.person2.name}, {data.aboutUsTeam.person2.title}</h3>
                            <p><a className="aboutUsTeam__link" href = {"tel:" + data.aboutUsTeam.person2.phone}>{data.aboutUsTeam.person2.phone}</a></p>
                            <p><a className="aboutUsTeam__link" href = {"mailto:" + data.aboutUsTeam.person2.email}>{data.aboutUsTeam.person2.email}</a></p>
                        </div>
                    </div>
                    
                </div>
                
            </div>
            
        </div>
    )
}

export default Contact;