import React from 'react';
import './ServicesHome.scss';

import serviceIcon from '../../Img/ServicesHome/pump.svg';
import waterNet from '../../Img/ServicesHome/water-network.svg';
import sewageNet from '../../Img/ServicesHome/sewage.svg';
import hydrant from '../../Img/ServicesHome/hydrant.svg';
import data from '../../Data/data.json';



const ServicesHome = () => {
   

    return (
        <div className="servicesHome py-5">
            <div className="container mb-5">
                <h2 className="mb-5 h2">{data.servicesHome.title}</h2>
                <div className="row servicesHome__content">
                    <div className="col-md-3 servicesHome__card">
                        <img src={waterNet} alt="servis" />
                        <h3 className="h3">{data.servicesHome.serviceCard.serviceOne.title}</h3>
                        <p>{data.servicesHome.serviceCard.serviceOne.desc}</p>
                    </div>
                    <div className="col-md-3 servicesHome__card">
                        <img src={sewageNet} alt="servis" />
                        <h3 className="h3">{data.servicesHome.serviceCard.serviceTwo.title}</h3>
                        <p>{data.servicesHome.serviceCard.serviceTwo.desc}</p>
                    </div>
                    <div className="col-md-3 servicesHome__card">
                        <img src={serviceIcon} alt="servis" />
                        <h3 className="h3">{data.servicesHome.serviceCard.serviceThree.title}</h3>
                        <p>{data.servicesHome.serviceCard.serviceThree.desc}</p>
                    </div>
                    <div className="col-md-3 servicesHome__card">
                        <img src={hydrant} alt="servis" />
                        <h3 className="h3">{data.servicesHome.serviceCard.serviceFour.title}</h3>
                        <p>{data.servicesHome.serviceCard.serviceFour.desc}</p>
                    </div>
                </div>
            </div>
        </div>
    )
}

export default ServicesHome;