import React from 'react';
import './AboutUsTeam.scss';
import data from '../../Data/data.json';

import teamOne from '../../Img/Team/nikolaTim.jpg';
import teamTwo from '../../Img/Team/ivanaTim.jpg';
import teamThree from '../../Img/Team/mladenTim.jpg';


const AboutUsTeam = () => {

    return (
        <div className="aboutUsTeam py-5">
            <div className="container mb-5">
                <h2 className="mb-5 h2">Upoznajte naš tim</h2>
                <div className="row aboutUsTeam__content">
                    <div className="col-md-4 aboutUsTeam__card">
                        <img className="w-100" src={teamOne} alt="team" />
                        <h3 className="h3">{data.aboutUsTeam.person1.name}, {data.aboutUsTeam.person1.title}</h3>
                        <p>{data.aboutUsTeam.person1.subtitle}</p>
                        <p><a className="aboutUsTeam__link" href = {"tel:" + data.aboutUsTeam.person1.phone}>{data.aboutUsTeam.person1.phone}</a></p>
                        <p><a className="aboutUsTeam__link" href = {"mailto:" + data.aboutUsTeam.person1.email}>{data.aboutUsTeam.person1.email}</a></p>
                    </div>
                    <div className="col-md-4 aboutUsTeam__card">
                        <img className="w-100" src={teamTwo} alt="team" />
                        <h3 className="h3">{data.aboutUsTeam.person2.name}, {data.aboutUsTeam.person2.title}</h3>
                        <p>{data.aboutUsTeam.person2.subtitle}</p>
                        <p><a className="aboutUsTeam__link" href = {"tel:" + data.aboutUsTeam.person2.phone}>{data.aboutUsTeam.person2.phone}</a></p>
                        <p><a className="aboutUsTeam__link" href = {"mailto:" + data.aboutUsTeam.person2.email}>{data.aboutUsTeam.person2.email}</a></p>
                    </div>
                    <div className="col-md-4 aboutUsTeam__card">
                        <img className="w-100" src={teamThree} alt="team" />
                        <h3 className="h3">{data.aboutUsTeam.person3.name}, {data.aboutUsTeam.person3.title}</h3>
                        <p>{data.aboutUsTeam.person3.subtitle}</p>
                        <p><a className="aboutUsTeam__link" href = {"tel:" + data.aboutUsTeam.person3.phone}>{data.aboutUsTeam.person3.phone}</a></p>
                        <p><a className="aboutUsTeam__link" href = {"mailto:" + data.aboutUsTeam.person3.email}>{data.aboutUsTeam.person3.email}</a></p>
                    </div>
                </div>
            </div>
        </div>
    )
}

export default AboutUsTeam;