import React from 'react';
import './Nav.scss';
import logo from '../../Img/Logo/logo.svg';
import $ from 'jquery';
import { NavLink } from 'react-router-dom';



const Nav = () => {

    $(document).ready(function(){
        $(".hamburger").click(function(){
          $(this).toggleClass("is-active");
        });
      });

    return (
        <nav className="navbar navbar-expand-lg navbar-dark navbar__container">
            <a className="navbar-brand" href="/porta-nova"><img src={logo} alt="Porta Nova Inženjering" /></a>
            <button className="navbar-toggler hamburger" id="hamburger" type="button" data-toggle="collapse" data-target="#navbarSupportedContent" aria-controls="navbarSupportedContent" aria-expanded="false" aria-label="Toggle navigation">
                <span class="line"></span>
                <span class="line"></span>
                <span class="line"></span>
            </button>

            <div className="collapse navbar-collapse justify-content-end" id="navbarSupportedContent">
                <ul className="navbar-nav navbar-nav__nav">
                <li className="nav-item">
                        <NavLink className="nav-link" to="/porta-nova">Početna</NavLink>
                    </li>
                    <li className="nav-item">
                        <NavLink className="nav-link" to="/porta-nova/o-nama">O nama</NavLink>
                    </li>
                    <li className="nav-item">
                        <NavLink className="nav-link" to="/porta-nova/o-nama">Projektovanje</NavLink>
                    </li>
                    <li className="nav-item">
                        <NavLink className="nav-link" to="/porta-nova/kontakt">Kontakt</NavLink>
                    </li>
                </ul>
        
            </div>
        </nav>

    )
}

export default Nav;