import React from 'react';
import './AboutUsHome.scss';
import data from '../../Data/data.json';
import aboutUsHome from '../../Img/AboutUsHome/about-us.gif';




const AboutUsHome = () => {
   

    return (
        <div className="container-fluid aboutUsHome aboutUsHome__img">
            <div className="row">
                <div className="col-md-6 p-0">
                    <img className="w-100" src={aboutUsHome} alt={data.aboutUsHome.title} />
                </div>
                <div className="col-md-6 p-0 d-flex align-items-center">
                    <div className="aboutUsHome__right">
                        <h2 className="h2">{data.aboutUsHome.title}</h2>
                        <hr />
                        <h3>{data.aboutUsHome.subtitle}</h3>
                        <p className="mt-5">{data.aboutUsHome.desc}</p>
                    </div>
                    
                </div>
            </div>
        </div>
    )
}

export default AboutUsHome;